.button {
  display: inline-flex;
  align-items: center;
  font-weight: 400;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  user-select: none;
  background-color: #2d3436;
  border: 1px solid #2d3436;
  color: #dfe6e9;
  padding: .375rem .75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: 0.25rem;
  transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, background-color .15s ease-in-out, box-shadow .15s ease-in-out;
  text-decoration: none;
  overflow: hidden;
  position: relative; }
  .button:focus {
    outline: none;
    box-shadow: 0 0 0 3px #aab5b8; }
  .button:hover {
    background-color: #394144;
    border-color: #394144; }
  .button:active {
    background-color: #5b6a6e;
    border-color: #5b6a6e; }
  .button_lg {
    padding: .5rem 1rem;
    font-size: 1.25rem;
    line-height: 1.5;
    border-radius: .3rem; }
  .button_sm {
    padding: .25rem .5rem;
    font-size: .875rem;
    line-height: 1.5;
    border-radius: .2rem; }
  .button__text, .button.oc-loading::after {
    position: relative;
    z-index: 2; }
  .button__icon {
    position: relative;
    z-index: 2; }
    .button__icon_left {
      margin-right: .5rem; }
    .button__icon_right {
      margin-left: .5rem; }
